body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  background-color: #fafafa;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, Open Sans, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

ul, p {
  margin: 0;
  padding: 0;
}

.list {
  list-style: none;
}

button {
  cursor: pointer;
  color: #fafafa;
  background-color: #757578;
  border: none;
  border-radius: 10px;
  padding: 5px 25px;
  font-size: 15px;
}

.search-form {
  text-align: center;
  height: 50px;
  background-color: #49bdcd;
  border-radius: 20px;
  justify-content: center;
  align-items: center;
  margin-bottom: 50px;
  display: flex;
}

.search-btn {
  margin-left: 10px;
  font-weight: 100;
}

.search-input {
  height: 25px;
  border: 1px solid rgba(255, 255, 255, .3);
  border-radius: 10px;
  padding: 0 15px;
}

.visually-hidden {
  white-space: nowrap;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
  -webkit-clip-path: inset(50%);
  clip-path: inset(50%);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.load-more {
  margin-left: auto;
  margin-right: auto;
  display: block;
}

